import React from 'react'
import { useSelector } from 'react-redux'

import ListHeader from './ListHeader'
import ListFooter from './ListFooter'
import ListCard from './ListCard'

function List({ id, title, index }) {
  const numOfListCards = useSelector((state) => state.listCard.numOfListCards);
  const listId = id;

  const onDrop = (event, cat) => {
    let id = event.dataTransfer.getData('id');

    console.log('onDrop: ',{ id });

    // let tasks = this.state.tasks.filter((task) => {
    //   if (task.name == id) {
    //     task.category = cat;
    //   }
    //   return task;
    // });

    // this.setState({
    //   ...this.state,
    //   tasks,
    // });
  };

  return (
    <div
      className='list'
      onDrop={(event) => {
        onDrop(event, id);
      }}
    >
      <ListHeader id={id} title={title} index={index} />
      {numOfListCards.map((listCard) =>
        listCard.listId === listId ? (
          <ListCard key={listCard.id} id={listCard.id} title={listCard.title} />
        ) : (
          ''
        )
      )}
      <ListFooter id={id} />
    </div>
  );
}

export default List
