import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { addListCard } from '../../redux';

function ListFooter({ id }) {
  const numOfListCards = useSelector((state) => state.listCard.numOfListCards);
  const dispatch = useDispatch();
  const numOfListCardsCount = numOfListCards.filter(item => item.listId === id)

  return (
    <div className='list-footer'>
      <button
        className='btn-add-card'
        onClick={() => dispatch(addListCard(id, ''))}
      >
        + Add {numOfListCardsCount.length > 0 ? 'another' : 'a'} card
      </button>
    </div>
  );
}

export default ListFooter
