import React, { useState } from 'react'
import { useDispatch } from 'react-redux';
import { deleteListCard, updateListCard } from '../../redux';

function ListCard({ id, title, index }) {
  const [listCardTitle, setListCardTitle] = useState('');
  const [showCardTitle, setShowCardTitle] = useState(false);
  const dispatch = useDispatch();

  const updateListCardTitle = () => {
    const listCardTitleValue = listCardTitle ? listCardTitle : 'New task';
    setShowCardTitle(true);
    dispatch(updateListCard(index, id, listCardTitleValue));
  };

  const handleChange = (event) => {
    setListCardTitle(event.target.value);
  };

  const handleBlur = () => {
    updateListCardTitle();
  };

  const handleEnter = (event) => {
    if (event.key === 'Enter') {
      updateListCardTitle();
    }
  };

  const editTitle = () => {
    setShowCardTitle(false);
  };

  const draggables = document.querySelectorAll('.list-card');
  const containers = document.querySelectorAll('.list');

  draggables.forEach((draggable) => {
    draggable.addEventListener('dragstart', () => {
      draggable.classList.add('dragging');
    });

    draggable.addEventListener('dragend', () => {
      draggable.classList.remove('dragging');
    });
  });

  containers.forEach((container) => {
    container.addEventListener('dragover', (event) => {
      event.preventDefault();
      const afterElement = getDragAfterElement(container, event.clientY);
      const draggable = document.querySelector('.dragging');
      if (afterElement == null) {
        // container.appendChild(draggable);
        container.querySelector('.list-footer').before(draggable);
      } else {
        container.insertBefore(draggable, afterElement);
      }
    });
  });

  function getDragAfterElement(container, y) {
    const draggableElements = [
      ...container.querySelectorAll('.list-card:not(.dragging)'),
    ];

    return draggableElements.reduce(
      (closest, child) => {
        const box = child.getBoundingClientRect();
        const offset = y - box.top - box.height / 2;
        if (offset < 0 && offset > closest.offset) {
          return { offset: offset, element: child };
        } else {
          return closest;
        }
      },
      { offset: Number.NEGATIVE_INFINITY }
    ).element;
  }

  return (
    <div className='list-card' draggable='true'>
      {showCardTitle ? (
        <div className='list-card-task' onClick={() => editTitle()}>
          {title}
        </div>
      ) : (
        <input
          autoFocus
          type='text'
          value={listCardTitle}
          onChange={(event) => handleChange(event)}
          onKeyPress={(event) => handleEnter(event)}
          onBlur={() => handleBlur()}
          placeholder='Add task'
        />
      )}
      <button
        className='btn-delete-card'
        onClick={() => dispatch(deleteListCard(id))}
      >
        &times;
      </button>
    </div>
  );
}

export default ListCard
