import { ADD_LIST_CARD, DELETE_LIST_CARD, UPDATE_LIST_CARD } from './listCardTypes';
import { v4 as uuidv4 } from 'uuid';

const initialState = {
  numOfListCards: []
}

const listCardReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_LIST_CARD:
      return {
        ...state,
        numOfListCards: [
          ...state.numOfListCards,
          {
            listId: action.id,
            id: uuidv4(),
            title: action.title,
            status: false,
          },
        ],
      };

    case UPDATE_LIST_CARD:
      return {
        ...state,
        numOfListCards: state.numOfListCards.map((item) =>
          item.id === action.id ? { ...item, title: action.title } : item
        ),
      };

    case DELETE_LIST_CARD:
      return {
        numOfListCards: state.numOfListCards.filter(
          (listCard) => listCard.id !== action.id
        ),
      };

    default:
      return state;
  }
}

export default listCardReducer
