import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { deleteList, updateList } from '../../redux';

function ListHeader({ id, title, index }) {
  const [listTitle, setListTitle] = useState('');
  const [showTitle, setShowTitle] = useState(false);
  const dispatch = useDispatch();

  const updateListTitle = () => {
    const listTitleValue = listTitle ? listTitle : 'New list';
    setShowTitle(true);
    dispatch(updateList(index, id, listTitleValue));
  }

  const handleChange = (event) => {
    setListTitle(event.target.value);
  };

  const handleBlur = () => {
    updateListTitle();
  };

  const handleEnter = (event) => {
    if (event.key === 'Enter') {
      updateListTitle()
    }
  };

  const editTitle = () => {
    setShowTitle(false);
  };

  return (
    <div className='list-header'>
      {showTitle ? (
        <h2 onClick={() => editTitle()}>{title}</h2>
      ) : (
        <input
          autoFocus
          type='text'
          value={listTitle}
          onChange={(event) => handleChange(event)}
          onKeyPress={(event) => handleEnter(event)}
          onBlur={() => handleBlur()}
          placeholder='Add list title'
        />
      )}
      <button className='action' onClick={() => dispatch(deleteList(id))}>
        •••
      </button>
    </div>
  );
}

export default ListHeader
