import { ADD_LIST, DELETE_LIST, UPDATE_LIST } from './listTypes';

export const addList = (title) => {
  return {
    type: ADD_LIST,
    title
  }
}

export const updateList = (index, id, title) => {
  return {
    type: UPDATE_LIST,
    index,
    id,
    title,
  };
}

export const deleteList = (listId) => {
  return {
    type: DELETE_LIST,
    id: listId,
  };
};