import React from 'react'
import { useSelector } from 'react-redux';

import List from './List';
import AddList from './AddList';

function Board() {
  const numOfLists = useSelector((state) => state.list.numOfLists);
  return (
    <div className='board'>
      {numOfLists.map((list, i) => (
        <List key={list.id} id={list.id} index={i} title={list.title} />
      ))}
      <AddList />
    </div>
  );
}

export default Board
