import { ADD_LIST, DELETE_LIST, UPDATE_LIST } from './listTypes';
import { v4 as uuidv4 } from 'uuid';

const initialState = {
  numOfLists: [],
};

const listReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_LIST:
      return {
        ...state,
        numOfLists: [
          ...state.numOfLists,
          {
            id: uuidv4(),
            title: action.title,
            status: false,
          },
        ],
      };

    case UPDATE_LIST:
      return {
        ...state,
        numOfLists: state.numOfLists.map((item) =>
          item.id === action.id ? { ...item, title: action.title } : item
        )
      };

    case DELETE_LIST:
      return {
        ...state,
        numOfLists: state.numOfLists.filter(
          (item) => item.id !== action.id
        ),
      };

    default:
      return state;
  }
};

export default listReducer;
