import {
  ADD_LIST_CARD,
  DELETE_LIST_CARD,
  UPDATE_LIST_CARD,
} from './listCardTypes';

export const addListCard = (id, title) => {
  return {
    type: ADD_LIST_CARD,
    id,
    title
  }
}

export const updateListCard = (index, id, title) => {
  return {
    type: UPDATE_LIST_CARD,
    index,
    id,
    title,
  };
};

export const deleteListCard = id => {
  return {
    type: DELETE_LIST_CARD,
    id
  };
}
