import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { addList } from '../../redux';

function AddList() {
  const numOfLists = useSelector((state) => state.list.numOfLists);
  const dispatch = useDispatch();
  const numOfListsCount = numOfLists.length;

  return (
    <button className='btn-add-list' onClick={() => dispatch(addList(''))}>
      + Add {numOfListsCount > 0 ? 'another' : 'new'} list
    </button>
  );
}

export default AddList;
